<template>
  <nav id="nav">
    <div id="topmenu">
      <router-link to="/" class="logo">EDU CHARM</router-link>
      <ul class="category">
        <li><router-link to="/Page3">안전</router-link></li>
        <li><router-link to="/Page3">마을</router-link></li>
        <li><router-link to="/Page3">생태/환경</router-link></li>
        <li><router-link to="/Page3">방송/미디어</router-link></li>
        <li class="work_point"><router-link to="/Page3">활동포인트</router-link></li>
        <li class="sp_point"><router-link to="/Page3">SP포인트</router-link></li>
      </ul>
    </div>
    <div id="topmember">
      <ul class="login">
        <li v-if="$store.state.isLogin"><router-link to="/Login">로그인</router-link></li>
        <li v-else><router-link to="/Login">로그아웃</router-link></li>
        <li v-if="$store.state.isLogin"><a href="#">회원가입</a></li>
        <li v-else><router-link to="/page8">마이페이지</router-link></li>
        <li><a href="#">장바구니</a></li>
      </ul>
    </div>
    <div class="top_bar bar_on" @click="[navShow(), barHide()]">
      <span :class="{'bar_off': barOff === 'bar_off'}"></span>
      <span :class="{'bar_off': barOff === 'bar_off'}"></span>
      <span :class="{'bar_off': barOff === 'bar_off'}"></span>
    </div>
    <div id="bar_menu" class="nav_off" :class="{'nav_on': isOff === 'nav_on'}">
      <div class="bar_wrap">
        <router-link to="/" class="bar_logo">EDU CHARM</router-link>
        <ul class="bar_member">
          <li><router-link to="/Login">로그인</router-link></li>
          <li><router-link to="/Login">회원가입</router-link></li>
          <li><router-link to="/Login">장바구니</router-link></li>
        </ul>
        <ul class="bar_category">
          <li><router-link to="/page3">안전</router-link></li>
          <li><router-link to="/page3">마을</router-link></li>
          <li><router-link to="/page3">생태/환경</router-link></li>
          <li><router-link to="/page3">방송/미디어</router-link></li>
          <li><router-link to="/page3">환경포인트</router-link></li>
          <li><router-link to="/page3">SP포인트</router-link></li>
        </ul>
        <div class="close_bar" @click="[navShow(), barHide()]">
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>

export default {
  name: 'Nav',
  data(){
    return{
      isOff: 'nav_off',
      barOff: 'bar_on'
    }
  },
  methods: {
    navShow(){
      if(this.isOff === 'nav_off'){
        this.isOff = 'nav_on'
      } else {
        this.isOff = 'nav_off'
      }
    },
    barHide(){
      if(this.barOff === 'bar_on'){
        this.barOff = 'bar_off'
      } else {
        this.barOff = 'bar_on'
      }
    }
  }
}
</script>

<style scoped>
@import './assets/reset.css';

#nav {
  width: 1920px; height: 110px;
  display: flex;
  border-bottom: 1px solid rgb(216, 216, 216);
  box-shadow: 0 1px 3px 0px rgb(240, 240, 240);
}
#topmenu {
  width: 80%;
  display: flex;
  align-items: center;
}
.logo {
  margin-left: 50px;
  font-size: 48px;
  font-weight: bold;
  color: rgb(21, 214, 150);
  text-decoration: none;
  box-sizing: border-box;
}
.category {
  padding: 0 20px;
  display: flex;
  align-items: center;
}
.category > li {
  line-height: 60px;
  text-align: center;
  font-size: 22px;
  font-weight: bold;
}
.category > li > a {
