}
#bar_menu {
  position: fixed;
  top: 0; left: 0;
  background-color: rgba(0,0,0,0.9);
  width: 100%; height: 100%;
  z-index: 90;
}
.bar_wrap {
  width: 100%; height: 100%;
  margin: 0 auto;
  text-align: center;
  padding: 100px;
