  font-weight: bold;
  color: rgb(21, 214, 150);
  display: inline-block;
  margin-bottom: 50px;
  box-sizing: border-box;
}
.bar_member {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}
.bar_member li {
  margin-left: 30px;
  font-size: 16px;
}
.bar_member li:hover {
  font-weight: bold;
}
.bar_member li:first-child {
  margin-left: 0;
}
.bar_category {
  font-size: 25px;
  font-weight: bold;
  padding: 50px;
  box-sizing: border-box;
}
.bar_category li {
  display: inline-block;
  width: 100%;
  margin-bottom: 40px;
  transition: all .5s;
}
.bar_category li:hover {
  color: rgb(162, 247, 218);
}
.close_bar {
  cursor: pointer;
}
.close_bar span::before {
  content: ''; display: block;
  width: 38px;
  border: 2px solid #fff;
}
.close_bar span:nth-child(1) {
  display: inline-block;
  transform: rotate(135deg);
  position: absolute;
  top: 48px; right: 37px;
}
.close_bar span:nth-child(2) {
  display: inline-block;
  transform: rotate(-135deg);
  position: absolute;
  top: 48px; right: 37px;
}

