import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      isLogin : true,
      isMypage : true,
    }
  },
  mutations: {
    changeLogin(state){
      state.isLogin = false
    },
    changeMypage(state){
      state.isMypage = false
    },
  }
})

export default store